import React from 'react';
import './styles.scss'

const listPages = [
    "Dashboard",
    "Customers",
    "Remittance",
    "P2P",
    "Transaction history",
    "Todos",
]

function SideBar() {
    return (
        <div className={"side-bar"}>
            {listPages.map(page => {
                return <a href={page.toLowerCase()}>{page}</a>
            })}
        </div>
    );
}

export default SideBar;
